.container {
  max-width: 1630px;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  outline: 1px solid brown;
}

.header__container {
  max-width: 1781px;
}
.header__top-bg {
  padding-top: 16px;
  padding-right: 50px;
  padding-bottom: 16px;
  padding-left: 67px;
  background: #121212;
}
.header__top {
  display: flex;
  align-items: center;
}
.header__search {
  margin-left: auto;
}
.header__btn {
  margin-left: auto;
}
.header__contacts {
  margin-left: 50px;
}
.header__bottom {
  display: flex;
}

.header-item {
  padding-top: 10px;
  padding-bottom: 10px;
}
.header-item:not(:last-child) {
  border-bottom: 0.5px solid rgba(255, 255, 255, 0.5);
  margin-bottom: 10px;
}

.header-link {
  font-size: clamp(0.875rem, 0.844rem + 0.16vw, 1rem);
}

.search {
  width: 512px;
  display: flex;
  align-items: center;
}
.search__input {
  position: relative;
  width: 100%;
  padding-top: 18px;
  padding-right: 10px;
  padding-bottom: 10px;
  font-size: 18px;
  color: rgba(255, 255, 255, 0.5);
  border: none;
  border-bottom: 1px solid rgba(255, 255, 255, 0.75);
  background-color: transparent;
}
.search__btn {
  margin-left: -17px;
  position: relative;
  z-index: 3;
  width: 17px;
  height: 17px;
  background: url(../images/icons/search.svg) center center/cover no-repeat;
}

.btn {
  border-radius: 3px;
  font-weight: 600;
  text-align: center;
  border: 1px solid transparent;
}
.btn--bg-red {
  padding: 15px 44px;
  color: #FFF;
  background: linear-gradient(135deg, #7a0000 0%, #900 100%);
  transition-duration: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
@media (any-hover: hover) {
  .btn--bg-red:hover {
    color: var(--solid);
    background: #FFF;
    border: 1px solid var(--solid);
  }
}

.phones {
  position: relative;
}
.phones__spoller {
  padding: 15px 44px;
  display: flex;
  align-items: center;
  gap: 5px;
  font-weight: 500;
  text-align: center;
  color: #FFF;
  transition-duration: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.phones__spoller.active {
  background-color: rgba(122, 0, 0, 0.85);
  color: #FFF;
}
.phones__arrow {
  margin-top: 3px;
  transition-duration: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.phones__svg {
  fill: #FFF;
}
.phones__link {
  display: flex;
  align-items: center;
  gap: 8px;
  transition-duration: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.phones__spoller.active .phones__arrow {
  rotate: 180deg;
}
.phones__list {
  position: absolute;
  width: 100%;
  padding: 20px;
  display: block;
  color: #FFF;
  background: rgba(122, 0, 0, 0.85);
  border-radius: 0.1875rem;
  box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.14);
  top: 46px;
  left: 0;
  z-index: 1;
  transform: translateY(-200%);
  opacity: 0;
  transition-duration: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.phones__list.active {
  transform: translateY(0%);
  opacity: 1;
}